import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import Experience from '../Experience';
import Skills from '../Skills';

class Resume extends Component {

    skills = () => {
        const skills = [
            {
                name: 'Javascript',
                level: 90,
            },
            {
                name: 'HTML',
                level: 70,
            },
            {
                name: 'Nodejs',
                level: 80,
            },
            {
                name: 'CSS/SCSS',
                level: 60,
            },
            {
                name: '.netcore',
                level: 90,
            },
            {
                name: 'C#',
                level: 100,
            },
            {
                name: 'Angular',
                level: 60,
            },
            {
                name: 'React',
                level: 80,
            },
            {
                name: 'SQL',
                level: 90,
            },
            {
                name: 'Docker',
                level: 70,
            },
            {
                name: 'Azure',
                level: 70,
            },
            {
                name: 'nginx',
                level: 60,
            },
            {
                name: 'redux',
                level: 70,
            },
            {
                name: 'graphql',
                level: 70,
            },
        ]

        return skills;
    }

    render() {
        return (
            <div>
                <Grid>
                    <Cell col={4} className={'resume-left-col'}>
                        <div style={{ textAlign: 'center' }} >
                            <img className="avatar-img" src="https://lh3.googleusercontent.com/-Q3uH4hr8ROk/WS1ocuSI13I/AAAAAAAAAPo/PisSjul7cV04GlQfog-s9PwWmEQI7tUaACEwYBhgL/w278-h280-p/rafal.jpg" alt="avatar"
                                style={{ height: '200px' }} />
                        </div>
                        <h2 style={{ paddingTop: '1em' }}> Rafał Pisarczyk </h2>
                        <h4 style={{ color: 'gray' }}> Full stack developer </h4>
                        <hr />
                        <p> Cooding since 2006</p>
                        <hr />
                        <h5>Address</h5>
                        <p> Kraków 30-718 </p>
                        <h5>Phone</h5>
                        <p> +(48) 690346759</p>
                        <h5>Email</h5>
                        <p>rafalpisarczyk@gmail.com</p>
                        <h5> Language </h5>
                        <p> English </p>
                        <p> Polish (native) </p>
                        <hr/>
                    </Cell>
                    <Cell className={'resume-right-col'} col={8}>
                        <h2>Education</h2>

                        <Experience
                            startYear={2006}
                            endYear={2011}
                            jobName={`University of Technology in Cracow`}
                            jobDescription={`Information Technology
                                            (master’s degree)`}
                        />

                        <Experience
                            startYear={2003}
                            endYear={2006}
                            jobName="High School number 1 in Busku Zdroju"
                            jobDescription="Extended math and infrmation technology"
                        />
                        <hr />

                        <h2>Experience</h2>

                        <Experience
                            startYear={2018}
                            endYear={'now'}
                            jobName="Lookmove.ch"
                            jobDescription="Senior Full Stack Web Devloper"
                        />

                        <Experience
                            startYear={2014}
                            endYear={2018}
                            jobName="Schibsted Tech Polska Sp. z.o.o"
                            jobDescription="Software Engineer"
                        />

                        <Experience
                            startYear={2011}
                            endYear={2014}
                            jobName="Qumak S.A"
                            jobDescription="Full Stack Devloper"
                        />

                        <hr/>
                        <h2>Skills</h2>
                        {this.skills().map(s => <Skills key={s.name} skill={s.name}
                            progress={s.level}
                        />)}
                    </Cell>
                </Grid>
            </div>
        );
    }
}

export default Resume;


