import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import cx from 'classnames';

class Landing extends Component {
    render() {
        return (
            <div style={{ width: '100%', margin: 'auto' }}>
                <Grid className={cx("landing-grid", 'ui-gradient')}>
                    <Cell col={12} >
                        <img
                            alt='avatar'
                            className="avatar-img"
                            src="https://lh3.googleusercontent.com/-Q3uH4hr8ROk/WS1ocuSI13I/AAAAAAAAAPo/PisSjul7cV04GlQfog-s9PwWmEQI7tUaACEwYBhgL/w278-h280-p/rafal.jpg" />

                        <div className="banner-text">
                            <h1>Full stack web developer </h1>
                            <hr />
                            <p> HTML | JavaScript | React | Angular | NodeJs | Express | Docker | Netcore | C# | SASS | Nginx | Azure | Redux </p>
                            <div className="social-links">
                                {/* linkedin */}
                                <a href="https://www.linkedin.com/in/rafa%C5%82-pisarczyk-07a53399/" target='_blank' rel='noopener noreferrer' >
                                    <i className="fa fa-linkedin-square"  aria-hidden='true' />
                                </a>
                                <a href="https://www.linkedin.com/in/rafa%C5%82-pisarczyk-07a53399/" target='_blank' rel='noopener noreferrer' >
                                    <i className="fa fa-linkedin-square"  aria-hidden='true' />
                                </a>
                            </div>
                        </div>
                    </Cell>
                </Grid>
            </div >
        );
    }
}

export default Landing;


